
usgsService.$inject = ['$http'];
export default function usgsService ($http) {
  const url = 'http://earthquake.usgs.gov/earthquakes/feed/v1.0/summary/all_month.geojson';
  return {
    get () {
      return $http.get(url)
        .then(res => res.data);
    }
  };
};


